@use 'abstracts' as *;
@use 'components/navigation';

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

html {
  font-family: $font-family;
  font-size: $font-size;
  font-weight: 400;
  line-height: $line-height;
}

// Components

body {
  margin: 0;
}

h1 {
  font-size: 3rem;
  font-weight: 500;
  line-height: $line-height-sm;
}

h2 {
  font-size: 2rem;
  font-weight: 500;
  line-height: $line-height-sm;
}

a {
  text-decoration: none;
  color: $color-black;
}

/* Common classes */
.container {
  @include container;
}

.container-sm {
  @include container-sm;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  overflow: hidden;
  clip: rect(0 0 0 0);
  visibility: hidden;
}

/* Header section */
.header {
  @include header;
}

/* Content section */
.content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: $container-secondary;
}

.content__title {
  margin-top: 0;
  margin-bottom: 38px;
}

.content__description {
  color: $color-secondary;
  margin-bottom: 57px;
}

.content__button {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 225px;
  min-height: 65px;
  background-color: $color-primary;
  text-transform: uppercase;
  color: $color-white;
  padding: 0.5em;
  letter-spacing: $letter-spacing;
}

/* Section styles */
.section {
  padding-top: 110px;
  padding-bottom: 90px;
  text-align: center;

  &__title {
    margin-bottom: 35px;
  }

  &__description {
    color: $color-secondary;
  }
}

/* About section */
.about {
  background-color: $color-white;
}

/* Works section */
.works {
  background-color: $color-gray-light;
}

/* Experience section */
.experience {
  @include experience;
}

.experience__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: $gap-xl;
}

.experience-item {
  display: flex;
  gap: $gap;
}

.experience-item__description {
  line-height: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.experience-item__number {
  font-size: 1.3125rem; /* 21px */
  font-weight: 500;
}

.experience-item__label {
  font-size: 1rem;
  line-height: 1.64;
}

.experience-item__picture {
  display: block;
  order: -1;
}

.experience-item__image {
  display: block;
  width: 50px;
  height: 50px;
}

/* Video section */
.video {
  @include video;
}

.video__iframe {
  width: 100%;
  height: auto;
  aspect-ratio: 16 / 9;
  border: 0;
  min-height: 200px;
}

/* Footer section */
.footer {
  @include footer;
}

/* Navigation */

/* Media queries */
@mixin update-container($width) {
  $container-primary: $width !global;
}

@media (max-width: 992px) {
  @include update-container(720px);

  .header {
    padding-top: 50px;
    padding-bottom: 80px;
  }

  .nav {
    margin-bottom: 70px;
  }

  .experience__container {
    justify-content: space-evenly;
    gap: $gap-lg;
  }
}

@media (max-width: 768px) {
  @include update-container(540px);

  .nav__list {
    column-gap: 15px;
  }

  .content {
    max-width: 100%;
  }
}

@media (max-width: 576px) {
  @include update-container(100%);

  .nav {
    margin-bottom: 50px;

    &__list {
      flex-direction: column;
      align-items: center;
      gap: 10px;
    }
  }

  .content {
    text-align: center;
    align-items: center;
  }
}

@media (max-width: 375px) {
  $font-size: 14px !global;
  $gap-xl: $gap-sm !global;

  .header {
    padding-top: 30px;
    padding-bottom: 50px;
  }

  .nav {
    margin-bottom: 40px;
  }

  .content__button {
    min-width: 180px;
    min-height: 45px;
    font-size: 0.875rem;
  }
}
